<template>
  <div id="transactionInquiry">
    <!-- Start Time -->
    <section class="prlmaryone">
     <div>{{$t("message.transferPayment.transactionInquiry.startdate")}}</div>
     <input v-model="selectTime.startTime" @click="selectTimes(selectTime[0])" :style="changeStyleOne" :placeholder="$t('message.transferPayment.transactionInquiry.enterStart')" readonly="readonly">
     <i class="transactionInquiry_calendar" ></i>
    </section>
    <!-- End Time -->
    <section class="prlmaryone" style="padding: 0.2rem 0.3rem 0 0.3rem;">
     <div>{{$t("message.transferPayment.transactionInquiry.deadline")}}</div>
     <input v-model="selectTime.endTime" @click="selectTimes(selectTime[1])" :style="changeStyleTwo" :placeholder="$t('message.transferPayment.transactionInquiry.enterEnd')" readonly="readonly">
     <i class="transactionInquiry_calendar" style="top:2.4rem"></i>
    </section>
    <!-- filtrate -->
    <section class="prlmaryone" style="padding: 0.2rem 0.3rem 0 0.3rem;">
     <div>{{$t("message.transferPayment.transactionInquiry.filtrate")}}</div>
     <input  v-model="refer" class="refer" style="width:50%">
     <div style="height:0.7rem;width:25%;font-size:0.26rem;line-height:0.7rem;text-align:center;">
       <div style="background-color:#5AC4CF;width:85%;height:100%;margin-left:0.2rem;border-radius: 0.1rem;color:#fff;" @click="SeeMore">{{$t("message.transferPayment.transactionInquiry.refer")}}</div>
    </div>
    </section>
    <!-- Deal list -->
    <section style=" padding: 0.2rem 0.3rem 0.5rem 0.3rem;" >
      <ul style="width:100%;">
        <li class="listStyle">
          <div>{{$t("message.transferPayment.transactionInquiry.date")}}</div>
          <p>{{$t("message.transferPayment.walletAddress")}}</p>
          <span>{{$t("message.transferPayment.remittanceAmount")}}</span>
        </li>
        <li v-show="showLoading && this.swtAddress!==''" style="width:100%;height:2rem;display:flex;align-items:center;justify-content:center;">
          <van-loading type="spinner" color="white"/>
        </li>
        <li v-show="this.swtAddress===''" style="width:100%;height:2rem;display:flex;align-items:center;justify-content:center;">
          <span style="font-size:0.26rem;">{{$t("message.PersonalCenter.importWallet")}}</span>
        </li>
        <li @click="toDetails(item)" class="listStyle" v-for="(item,index) in tradingRecord" :key="index">
          <div>{{item.time}}</div>
          <p style='font-family:"Monaco","Consolas","monospace";font-size:0.26rem;'>{{item.walletAddress}}</p>
          <span>{{item.payAmount}}</span>
          <i class="personal_rightArrow"></i>
        </li>
      </ul>
    </section>
    <!-- dateTime -->
    <section>
      <van-popup v-model="showDateTimePopup">
        <div style="width:600px;height:300px;font-size:0.26rem">
          <van-datetime-picker style="font-size:0.3rem" v-model="currentDate" type="date" :min-date="minDate" 
          :item-height="50" @confirm="confirm(excessive)" @cancel="cancel" 
          :confirm-button-text="$t('message.transferPayment.transactionInquiry.confirm')"
          :cancel-button-text="$t('message.transferPayment.transactionInquiry.cancel')"/>
        </div>
      </van-popup>
    </section>
    <!-- Billing Details -->
    <section >
      <van-popup v-model="showDetailPopup" position="left" style="width:100%;height:100%;" :overlay="false">
          <ul style="width:100%;">
            <li class="top" >
              <img :src="leftArrow" @click="goback"> 
              <span>{{$t("message.transferPayment.transactionInquiry.billingDetails")}}</span>
            </li>
            <li v-for="(item,index) in orderDetails" :key="index" class="BillingDetails">
              <label>{{item.name}}</label><div>{{item.value}}</div>
            </li>
          </ul>
      </van-popup>
    </section>
    <section></section>
  </div>
</template>
<script>
import leftArrow from "@/images/language_left.png";
import { JcExchange } from "jcc_rpc";
import { jNodeConfig } from "jcc_mixins";
import { formatTime } from "js/utils";

export default {
  name: "transactionInquiry",
  mixins: [jNodeConfig],
  data() {
    return {
      leftArrow,
      showLoading: true,
      loading: false,
      excessive: "",
      showDateTimePopup: false,
      showDetailPopup: false,
      refer: "",
      minDate: new Date(2018, 10, 1),
      maxDate: new Date(),
      currentDate: new Date(),
      changeDate: "",
      pageRemark: {
        ledger: null,
        seq: null
      },
      selectTime: [
        {
          startTime: "",
          startTimeTip: "1"
        },
        {
          endTime: "",
          endTimeTip: "2"
        }
      ],
      tradingRecord: [],
      orderDetails: {
        indentNumber: {
          name: this.$t(
            "message.transferPayment.transactionInquiry.indentNumber"
          ),
          value: "--"
        },
        remittanceAmount: {
          name: this.$t("message.transferPayment.remittanceAmount"),
          value: "--"
        },
        remittanceWallet: {
          name: this.$t("message.transferPayment.remittanceWallet"),
          value: "--"
        },
        transactionCost: {
          name: this.$t("message.transferPayment.transactionCost"),
          value: "--"
        },
        exchangeRate: {
          name: this.$t("message.transferPayment.exchangeRate"),
          value: "--"
        },
        payee: {
          name: this.$t("message.transferPayment.payee"),
          value: "--"
        },
        receivableAmount: {
          name: this.$t("message.transferPayment.receivableAmount"),
          value: "--"
        },
        receivableWallet: {
          name: this.$t("message.transferPayment.receivableWallet"),
          value: "--"
        },
        purpose: {
          name: this.$t("message.transferPayment.purpose"),
          value: "--"
        },
        result: {
          name: this.$t("message.transferPayment.transactionInquiry.result"),
          value: "--"
        },
        date: {
          name: this.$t("message.transferPayment.transactionInquiry.date"),
          value: "--"
        }
      }
    };
  },
  beforeDestory() {
    window.removeEventListener("");
  },
  mounted() {
    if (this.swtAddress) {
      this.listenScroll();
      setTimeout(() => {
        this.getTransferHistory();
      }, 300);
    } else {
    }
  },
  computed: {
    changeStyleOne: function() {
      if (this.selectTime[0].startTime !== this.changeDate) {
        return "border-color:#A5C3FA;color:#373e44";
      }
    },
    changeStyleTwo: function() {
      if (this.selectTime[1].endTime !== this.changeDate) {
        return "border-color:#A5C3FA;color:#373e44";
      }
    },
    swtAddress() {
      return this.$store.getters.swtAddress || "";
    },
    coins() {
      return this.$store.getters.coins;
    }
  },
  methods: {
    listenScroll() {
      let vm = this;
      window.addEventListener("scroll", function(e) {
        let w =
          document.documentElement.clientHeight || document.body.clientHeight;
        let t = document.documentElement.scrollTop || document.body.scrollTop;
        let h =
          document.documentElement.scrollHeight || document.body.scrollHeight;
        console.log(w, t, h);
        console.log(vm.loading, w + t === h);
        if (!vm.loading && w + t === h) {
          vm.loading = true;
          vm.getTransferHistory();
        }
      });
    },
    async getTransferHistory() {
      this.loading = true;
      console.log("getList");
      let getCount = 0;
      let instOrder = new JcExchange(this.exHosts, this.exPort, this.https);
      let res = await instOrder.getHistoricPayments(
        this.swtAddress,
        this.pageRemark.ledger,
        this.pageRemark.seq
      );
      console.log(res);
      if (res && res.code === "0") {
        this.pageRemark.ledger = res.data.marker.ledger;
        this.pageRemark.seq = res.data.marker.seq;
        getCount = this.handleTxHistory(res.data.transactions);
      } else {
        console.log("error");
      }
      console.log("loadingfalse");
      if (getCount > 0) {
        this.loading = false;
      } else {
        setTimeout(() => {
          this.loading = false;
        }, 3000);
      }
      this.showLoading = false;
    },
    handleTxHistory(datas) {
      console.log("handleList");
      let count = 0;
      datas.forEach(el => {
        let coin = this.coins.find(coin => {
          return coin.label === el.currency;
        });
        if (!coin) {
          return;
        }
        el.time = formatTime(parseInt(el.time) * 1000, 4);
        el.isOut = el.sender === this.swtAddress;
        let addr = el.isOut ? el.receiver : el.sender;
        el.walletAddress = addr.substr(0, 5) + "****" + addr.substr(-5);
        el.payAmount = el.isOut
          ? "-" + el.amount + el.currency
          : "+" + el.amount + el.currency;
        el.amount = el.amount + el.currency;
        this.tradingRecord.push(el);
        count++;
      });
      return count;
    },
    selectTimes(item) {
      if (this.selectTime[0].startTimeTip === item.startTimeTip) {
        this.showDateTimePopup = true;
        this.excessive = "0";
        console.log("");
      } else {
        this.showDateTimePopup = true;
        this.excessive = "1";
        console.log("");
      }
    },
    // 
    confirm(excessive) {
      console.log(excessive);
      if (excessive === "0") {
        this.change();
        this.selectTime.startTime = this.changeDate;
      } else {
        this.change();
        this.selectTime.endTime = this.changeDate;
      }
      this.showDateTimePopup = false;
    },
    // 
    cancel() {
      console.log("");
      this.showDateTimePopup = false;
    },
    //
    change() {
      var date = this.currentDate;
      var seperator1 = "-";
      var seperator2 = ":";
      var month = date.getMonth() + 1;
      var strDate = date.getDate();
      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
      }
      this.changeDate =
        date.getFullYear() + seperator1 + month + seperator1 + strDate;
    },
    SeeMore() {
      this.selectTime.startTime = "";
      this.selectTime.endTime = "";
      console.log("");
    },
    toDetails(item) {
      console.log(item.sender);
      this.orderDetails.indentNumber.value = item.hash;
      this.orderDetails.remittanceAmount.value = item.amount;
      this.orderDetails.remittanceWallet.value = item.sender;
      this.orderDetails.receivableAmount.value = item.amount;
      this.orderDetails.receivableWallet.value = item.receiver;
      this.orderDetails.date.value = item.time;
      this.showDetailPopup = true;
    },
    goback() {
      this.showDetailPopup = false;
    }
  }
};
</script>
<style lang="scss" scoped>
.prlmaryone {
  padding: 1.28rem 0.3rem 0 0.3rem;
  text-align: left;
  display: flex;
  div {
    height: 0.7rem;
    width: 25%;
    font-size: 0.26rem;
    color: #373e44;
    line-height: 0.7rem;
  }
  input {
    height: 0.7rem;
    width: 75%;
    font-size: 0.26rem;
    color: #bdbebf;
    padding: 0 0.2rem;
    border: 0.01rem solid #bdbebf;
    border-radius: 0.1rem;
  }
}
.listStyle {
  width: 100%;
  height: 0.7rem;
  border-bottom: 1px solid #dadfe3;
  display: flex;
  font-size: 0.26rem;
  color: #555b61;
  align-items: center;
  div {
    width: 28%;
    text-align: left;
  }
  p {
    width: 40%;
    padding: 0;
    margin: 0;
  }
  span {
    width: 28%;
    text-align: right;
  }
}
.refer {
  height: 0.7rem;
  font-size: 0.26rem;
  color: #bdbebf;
  border: 0.01rem solid #bdbebf;
  border-radius: 0.1rem;
}
.top {
  width: 100%;
  height: 0.88rem;
  text-align: center;
  line-height: 0.8rem;
  font-size: 0.3rem;
  color: #fff;
  background-color: #005c9c;
  img {
    width: 0.24rem;
    height: 0.36rem;
    position: absolute;
    left: 0.2rem;
    top: 0.24rem;
  }
}
.BillingDetails {
  box-sizing: border-box;
  width: 100%;
  padding: 0.2rem 0.2rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  border-bottom: 1px solid #ebebed;
  font-size: 0.26rem;
  color: #5b636a;
  label {
    width: 40%;
    text-align: left;
  }
  div {
    width: 60%;
    text-align: right;
    word-break: break-all;
  }
}
.van-loading__spinner {
  color: #5b636a;
}
</style>



