<template>
  <div id="TransferPayment" style="width:100%;height:auto;background-color:#fafafa;">
    <div style="width:100%;position:fixed;top:0;z-index:1;">
      <ul class="transferHead">
        <li v-for="(item,index) in list" :key="index" @click="switchTab(item)" :class="{'li-clicked':currentShow===item.name}">{{item.title}}</li> 
      </ul>
    </div>
    <component :is="currentShow" :userName="userName" :address="address"></component>
  </div>
</template>

<script>
import agiotage from "./agiotage";
import payment from "./payment";
import transactionInquiry from "./transactionInquiry";
import bus from "@/js/bus";
export default {
  name: "TransferPayment",
  components: { agiotage, payment, transactionInquiry },
  created() {
    bus.$emit("currentRouteName", "TransferPayment");
    let currentShow = this.$route.query.currentShow;
    if (currentShow) {
      this.currentShow = currentShow;
      this.address = this.$route.query.address;
      this.userName = this.$route.query.name;
    }
  },
  data() {
    return {
      currentShow: "agiotage",
      userName: "",
      address: "",
      list: [
        {
          name: "agiotage",
          title: this.$t("message.transferPayment.agiotage")
        },
        {
          name: "payment",
          title: this.$t("message.transferPayment.payment")
        },
        {
          name: "transactionInquiry",
          title: this.$t("message.transferPayment.tradeInquiry")
        }
      ]
    };
  },
  methods: {
    switchTab(item) {
      this.currentShow = item.name;
      this.userName = "";
      this.address = "";
    }
  }
};
</script>

<style lang="scss" scoped>
.transferHead {
  display: flex;
  height: 0.88rem;
  width: 100%;
  align-items: center;
  border: 0.01rem solid #dadfe3;
  background-color: #fff;
  color: #5f666c;
  font-size: 0.28rem;
  li {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 33.33%;
  }
}
.li-clicked {
  background-color: #005c9c;
  color: #fff;
}
</style>





