<template>
  <div id="agiotage">
    <!-- Select Holding or Target Currency -->
    <ul class="selectTarget">
      <li @click="selectedHolding=true" :class="{'selectedHolding':false}">{{$t("message.transferPayment.HoldingCurrency")}}</li>
      <li @click="selectedHolding=false" :class="{'selectedHolding':false}">{{$t("message.transferPayment.TargetCurrency")}}</li>
    </ul>
    <!-- Select Currency -->
    <ul class="selectCurrency">
      <li @click="baseChoosing = !baseChoosing;counterChoosing=false;">
        <i :class="`currency-icon-${base}`"></i>
        <span>{{base}}</span>
        <i :class="`triangle_${baseChoosing?'clicked':'default'}`"></i>
      </li>
      <li @click="counterChoosing = !counterChoosing;baseChoosing=false;">
        <i :class="`currency-icon-${counter}`"></i>
        <span>{{counter}}</span>
        <i :class="`triangle_${counterChoosing?'clicked':'default'}`"></i>
      </li>
    </ul>
    <!-- selector -->
    <div style="display:flex;">
      <div style="position:relative;width:50%;">
        <div v-show="baseChoosing" style="position:absolute;top:100%;left:50%;margin-left:-0.95rem;margin-top:-0.15rem;">
          <JcSelect v-model="base" :data="currencyList" :align="'left'" :width="'1.9rem'" :borderColor="'#559df7'" :itemSpacing="'0.1rem'" :fontSize="'0.30rem'" @close="baseChoosing=false;"></JcSelect>
        </div>
      </div>
      <div style="position:relative;width:50%;">
        <div v-show="counterChoosing" style="position:absolute;top:100%;left:50%;margin-left:-0.95rem;margin-top:-0.15rem;">
          <JcSelect v-model="counter" :data="currencyList" :align="'left'" :width="'1.9rem'" :borderColor="'#559df7'" :itemSpacing="'0.1rem'" :fontSize="'0.30rem'" @close="counterChoosing=false;"></JcSelect>          
        </div>
      </div>
    </div>
    <!-- Input Amount to Agiotage -->
    <div class="amountInput">
      <div>
        <input type="number" v-model="form.amount" @input="holdingInputChange" @focus="form.amount=''" @blur="holdingInputBlur">
      </div>
      <p style="height:78%;border:0.015rem solid #e0e3e8;"></p>
      <div style="justify-content:flex-end;">
        <input type="number" v-model="form.inputTargetCount" style="text-align:right;" @input="targetInputChange" @focus="form.inputTargetCount=''" @blur="targetInputBlur">
      </div>
    </div>
    <!-- Show Exchange Rate -->
    <ul class="exchangeRate">
      <li style="justify-content:flex-end;">{{$t("message.transferPayment.exchangeRate")}}</li>
      <li style="min-width:30%;background-color:#e8eaf0;padding:0.05rem 0;border-radius:0.1rem;text-indent:0.2rem;color:#4d4d4d;">{{currentRatePrice}}</li>
      <!-- To Compare Exchange Rate  -->
      <li style="border-radius:0.22rem;color:#fff;background-color:#559df7;margin-left:0.2rem;padding:0.04rem 0.1rem;justify-content:center;">{{$t("message.transferPayment.exchangeRateCompare")}}</li>
    </ul>
    <!-- Show Transaction Summary -->
    <ol class="transactionSummary">
      <li style="color:#555b61;justify-content:center;">{{$t("message.transferPayment.transactionSummary")}}</li>
      <li>
        <span>{{$t("message.transferPayment.remittanceAmount")}}</span>
        <span>{{form.amount||0}} <span class="monoSpace">{{base}}</span></span>
      </li>
      <li>
        <span>{{$t("message.transferPayment.transactionCost")}}</span>
        <span>{{getTrandeCostPrice||0}}<span class="monoSpace">{{"SWTC"}}</span></span>
      </li>
      <li>
        <span>{{$t("message.transferPayment.amountOfConversion")}}</span>
        <span>{{getAmountOfConvert||0}} <span class="monoSpace">{{counter}}</span></span>
      </li>
      <li>
        <span>{{$t("message.transferPayment.receivableAmount")}}</span>
        <span>{{getReceivableAmount||0}} <span class="monoSpace">{{counter}}</span></span>
      </li>
    </ol>
    <!-- Input Information and Confirm payment -->
    <ol class="inputInformation">
      <li style="width:100%;position:relative;">
        <!-- Input Payee Name -->
        <label>{{$t("message.transferPayment.payee")}}</label>
        <!-- <div style="position:relative;width:70%;margin-right:0.15rem;"> -->
          <input v-model="form.payeeName" type="text" style="margin-right:0.2rem;">
          <!-- Contacts icon -->
          <i class="contacts_icon positionIcon" style="margin-top: -0.15rem;right:0.5rem;" @click="toContact"></i>
        <!-- </div> -->
        <!-- Scaning icon -->
        <i class="scanning_icon" style="min-width:0.32rem;"></i>
      </li>
      <li style="align-items:flex-start;">
        <!-- Input wallet Address -->
        <label style="margin-top:0.1rem;">{{$t("message.transferPayment.walletAddress")}}</label>
        <textarea v-model="form.walletAddress" style="width:100%;height:0.9rem;padding:0.1rem 0;vertical-align:middle;outline:none;"></textarea>
      </li>
      <li>
        <!-- Choose Purpose -->
        <label style="margin-top:0.1rem;">{{$t("message.transferPayment.purpose")}}</label>
        <div style="position:relative;width:100%;">
          <input style="width:100%;" v-model="form.purpose" type="text">
          <div @click="choosingPurpose=!choosingPurpose;" style="position:absolute;top:0;right:0;width:0.8rem;height:0.7rem;">
            <i class="blackArrow_icon positionIcon" :style="{'margin-top':choosingPurpose?'-0.1rem':'-0.12rem','transform':choosingPurpose?'rotate(90deg)':'rotate(0deg)'}"></i>
          </div>
          <div style="width:100%;position:absolute;top:0.9rem;left:0;z-index:1;">
            <JcSelect v-show="choosingPurpose" v-model="form.purpose" @close="choosingPurpose=false;" :data="purposeList" :width="'100%'" :borderColor="'#559df7'" :align="'left'" :fontSize="'0.28rem'" style="color:#616569;"></JcSelect>
          </div>
        </div>
      </li>
      <li style="justify-content:center;">
        <button @click="confirmDialog = true;" class="confirmBtn" :class="{'confirm_Btn':checkInput}">{{$t("message.transferPayment.confirmPay")}}</button>
      </li>
    </ol>
    <van-dialog v-model="confirmDialog" :show-confirm-button="false"  :title="$t('message.transferPayment.payments.inputPassword')" style="margin-top:-15%;">
      <input v-model="form.tradePsw" class="inputPsw" type="password" style="margin-top:0.1rem;" :placeholder="$t('message.transferPayment.payments.inputPassword')">
      <div><button @click="confirmPay" class="inputPsw" style="margin:0.4rem 0 0.2rem 0;background:#409EFF;color:#fff;font-size:0.28rem;">{{$t("message.transferPayment.comfirmAgiotagePay")}}</button></div>
    </van-dialog>
  </div>
</template>
<script>
import { BigNumber } from "bignumber.js";
import JcSelect from "@/components/jc_select";
import Jexchage from "@/mixins/exchage";
import { getUserBalance } from "js/fetch";
import { Toast } from "vant";
export default {
  name: "agiotage",
  components: { JcSelect },
  mixins: [Jexchage],
  data() {
    return {
      selectedHolding: true,
      choosingPurpose: false,
      confirmDialog: false,
      form: {
        amount: 0,
        inputTargetCount: "",
        tradePsw: "",
        payeeName: "",
        walletAddress: "",
        purpose: this.$t("message.transferPayment.overseasInvestment")
      },
      base: "CNT",
      baseChoosing: false,
      counter: "EGP",
      counterChoosing: false,
      type: 0,
      purposeList: [
        { name: this.$t("message.transferPayment.overseasInvestment") },
        { name: this.$t("message.transferPayment.crossBorderTransfer") },
        { name: this.$t("message.transferPayment.crossBorderPayment") }
      ],
      currencyList: [],
      currencyPairs: {
        base: "",
        counter: ""
      }
    };
  },
  props: {
    userName: {
      type: String,
      default: ""
    },
    address: {
      type: String,
      default: ""
    }
  },
  created() {
    this.form.payeeName = this.userName;
    this.form.walletAddress = this.address;
  },
  mounted() {
    this.form.amount = 1000;
    this.form.inputTargetCount = this.getAmountOfConvert;
    setTimeout(() => {
      this.getCurrencyListList();
    }, 300);
  },
  methods: {
    confirmPay() {
      this.confirmDialog = false;
      if (!this.checkInput || this.loading) {
        return;
      }
      this.loading = true;
      Toast.loading({
        duration: 0,
        forbidClick: true,
        message: this.$t("message.transferPayment.exchangeing")
      });
      this.confirmSubmit(this.form.tradePsw)
        .then(() => {
          setTimeout(() => {
            Toast.loading({
              duration: 0,
              forbidClick: true,
              message: this.$t("message.transferPayment.transfering")
            });
          }, 2500);
          setTimeout(() => {
            this.checkBalance(this.counter, this.form.inputTargetCount);
          }, 12500);
        })
        .catch(err => {
          console.log(err);
          this.clearInput();
        });
    },
    async checkBalance(currency, amount) {
      let coin = this.getBaseName(currency);
      console.log("balance-coin=" + coin, "balance-amount=" + amount);
      console.log("balance-addr=" + this.publicKey);
      let balanceList = await getUserBalance(this.publicKey);
      let enoughToPay = false;
      if (Array.isArray(balanceList.data) && balanceList.data.length > 0) {
        // currencyamount
        balanceList.data.forEach(el => {
          if (
            el.currency === coin &&
            parseFloat(el.value) >= parseFloat(amount)
          ) {
            enoughToPay = true;
          }
        });
        if (enoughToPay) {
          console.log("");
          this.orderWithtradePsw(coin);
        } else {
          console.log("");
          Toast(this.$t("message.transferPayment.autoTransferFialed"));
        }
      } else {
        Toast(this.$t("message.transferPayment.autoTransferFialed"));
      }
    },
    toContact() {
      // console.log(this.contact);
      this.$router.push({
        path: "/contact",
        query: {
          currentShow: "agiotage"
        }
      });
    },
    waitPayment() {},
    holdingInputChange(e) {
      let val = e.target.value;
      if (!val || val === "" || val <= 0) {
        return;
      }
      this.form.inputTargetCount = new BigNumber(this.getAmountOfConvert)
        .decimalPlaces(5)
        .toNumber();
    },
    holdingInputBlur() {
      let val = this.form.amount;
      if (!val || val <= 0 || val === "") {
        this.form.amount = this.getAmountByConvertCount;
      }
    },
    targetInputChange(e) {
      let val = e.target.value;
      if (!val || val === "" || val <= 0) {
        return;
      }
      this.form.amount = new BigNumber(this.getAmountByConvertCount)
        .decimalPlaces(5)
        .toNumber();
    },
    targetInputBlur() {
      let val = this.form.inputTargetCount;
      if (!val || val <= 0 || val === "") {
        this.form.inputTargetCount = this.getAmountOfConvert;
      }
    },
    getCurrencyListList() {
      let list = this.coinList;
      if (Array.isArray(list) && list.length > 0) {
        list.forEach(el => {
          this.currencyList.push({
            name: el.label,
            icon: `currency-icon-${el.label}`
          });
        });
      }
    },
    clearInput() {
      this.form.tradePsw = "";
      this.loading = false;
    }
  },
  computed: {
    coinList() {
      return this.$store.getters.coins;
    },
    getTrandeCostPrice() {
      return new BigNumber(0.01);
    },
    getAmountOfConvert() {
      let amount = this.form.amount;
      if (amount !== "" && amount > 0) {
        return new BigNumber(this.form.amount)
          .multipliedBy(new BigNumber(this.currentRatePrice))
          .decimalPlaces(3)
          .toNumber();
      }
    },
    getAmountByConvertCount() {
      let amount = this.form.inputTargetCount;
      if (amount !== "" && amount > 0) {
        return new BigNumber(this.form.inputTargetCount)
          .dividedBy(this.currentRatePrice)
          .decimalPlaces(3)
          .toNumber();
      }
    },
    getReceivableAmount() {
      return new BigNumber(this.getAmountOfConvert).decimalPlaces(3).toNumber();
    },
    // when selected the currecny, this will set form.price and trade type (-1 = cannot trade; 0 = buy; 1 = sell)
    // return the exchange rate by computed price
    currentRatePrice() {
      let price = 1;
      if (this.base === "CNT") {
        this.currencyPairs.counter = "CNY";
        this.currencyPairs.base = this.counter;
      } else if (this.counter === "CNT") {
        this.currencyPairs.counter = "CNY";
        this.currencyPairs.base = this.base;
      }
      if (this.base === this.counter) {
        this.type = -1;
        price = new BigNumber(1).decimalPlaces(3).toNumber();
      } else if (this.base === "CNT" && this.counter === "EGP") {
        this.type = 0;
        this.form.price = new BigNumber(0.389).decimalPlaces(3).toNumber();
        price = new BigNumber(1)
          .dividedBy(new BigNumber(0.389))
          .decimalPlaces(3)
          .toNumber();
      } else if (this.base === "EGP" && this.counter === "CNT") {
        this.type = 1;
        this.form.price = new BigNumber(0.388).decimalPlaces(3).toNumber();
        price = new BigNumber(0.388).decimalPlaces(3).toNumber();
      }
      console.log("type=" + this.type);
      console.log("price=" + this.form.price);
      console.log("sum=" + this.sum);
      console.log("pairs=", this.currencyPairs);
      return price;
    },
    getTradeCostRate() {
      let rate = 0.005;
      return new BigNumber(rate).decimalPlaces(5).toNumber();
    },
    checkInput() {
      return (
        this.form.amount > 0 &&
        this.form.price > 0 &&
        this.form.inputTargetCount > 0 &&
        this.form.payeeName !== "" &&
        this.form.walletAddress !== "" &&
        this.form.purpose !== "" &&
        this.base !== "" &&
        this.counter !== ""
      );
    }
  },
  watch: {
    currentRatePrice(newVal) {
      this.form.inputTargetCount = this.getAmountOfConvert;
    }
  }
};
</script>
<style lang="scss" scoped>
#agiotage {
  display: flex;
  flex-flow: column;
  padding: 0.88rem 0.4rem 0;
}
.selectTarget {
  display: flex;
  width: 100%;
  font-size: 0.3rem;
  margin-top: 0.2rem;
  color: #5e6469;
  li {
    width: 50%;
    padding-bottom: 0.15rem;
    border-bottom: 0.04rem solid #e0e3e8;
  }
  .selectedHolding {
    color: #559df7;
    border-bottom: 0.04rem solid #559df7;
    font-weight: bold;
  }
}
.selectCurrency {
  display: flex;
  width: 100%;
  font-size: 0.3rem;
  margin-top: 0.1rem;
  li {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50%;
    padding: 0.3rem 0;
    i {
      position: relative;
      top: -0.02rem;
    }
    span {
      margin: 0 0.2rem 0 0.1rem;
    }
  }
}
.amountInput {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 0.88rem;
  width: 86%;
  margin: 0 auto;
  border: 0.02rem solid #005c9c;
  color: #005c9c;
  border-radius: 0.44rem;
  font-size: 0.3rem;
  font-weight: bold;
  div {
    display: flex;
    align-items: center;
    width: 42%;
    height: 78%;
    input {
      width: 100%;
      border: none;
      background-color: #fafafa;
    }
  }
}
.exchangeRate {
  width: 100%;
  min-height: 0.44rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0.4rem auto 0;
  color: #373e44;
  font-size: 0.26rem;
  li {
    height: 100%;
    display: flex;
    align-items: center;
    white-space: nowrap;
  }
}
.transactionSummary {
  display: flex;
  flex-flow: column;
  margin-top: 0.5rem;
  .monoSpace {
    font-family: "Consolas", "Monaco", "monospace";
  }
  li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #6e7880;
    font-size: 0.28rem;
    margin-bottom: 0.15rem;
  }
  li:last-child,
  li:first-child {
    border-bottom: 0.02rem solid #f1f1f1;
    padding-bottom: 0.16rem;
  }
}
.inputInformation {
  width: 100%;
  font-size: 0.26rem;
  color: #4d4d4d;
  li {
    width: 100%;
    display: flex;
    align-items: center;
    margin: 0.2rem 0;
    label {
      min-width: 1.3rem;
      max-width: 1.5rem;
      text-align: left;
    }
    input,
    textarea {
      width: 100%;
      height: 0.7rem;
      border: 0.01rem solid #b1bec6;
      border-radius: 0.1rem;
      text-indent: 0.2rem;
      font-size: 0.3rem;
      &:focus {
        border: 0.01rem solid #559df7;
      }
    }
    .positionIcon {
      position: absolute;
      top: 50%;
      right: 0;
      margin-right: 0.2rem;
    }
  }
}
.confirmBtn {
  height: 0.76rem;
  min-width: 32%;
  padding: 0 0.2rem;
  background-color: #3f7bff;
  color: #fff;
  margin: 0.1rem 0 3.2rem 0;
  border-radius: 0.1rem;
  opacity: 0.7;
}
.confirm_Btn {
  opacity: 1;
}
.inputPsw {
  width: 90%;
  height: 0.68rem;
  border: none;
  border-radius: 0.06rem;
  font-size: 0.22rem;
  text-indent: 0.2rem;
}
</style>



