import { isNumber } from "js/utils";
import { BigNumber } from "bignumber.js";
import { createOrder } from 'jcc_exchange';
import { transferAccount } from "jcc_exchange";
import { jNodeConfig } from "jcc_mixins";
import { JcWalletTool } from "jcc_wallet";
import { Wallet } from "jcc_jingtum_base_lib";
import { Toast } from "vant"
export default {
  mixins: [jNodeConfig],
  methods: {
    async confirmSubmit(password) {
      let inst = new JcWalletTool(this.jcWallet);
      let proEnv = process.env.NODE_ENV === "production";
      let secret = null;
      try {
        secret = await inst.validatePassword(password, 'swt');
      } catch (err) {
        Toast.fail(this.$t("message.transferPayment.passwordWrong"))
        return;
      }
      return new Promise((resolve, reject) => {
        createOrder({
          counter: this.currencyPairs.counter,
          base: this.currencyPairs.base,
          issuer: process.env.currencies.UST.issuer,
          address: this.publicKey,
          secret: secret,
          type: this.type,
          amount: this.getAmount,
          sum: this.sum,
          hosts: proEnv ? this.exHosts : [window.location.hostname],
          port: proEnv ? this.exPort : "8080",
          https: this.https
        }).then(() => {
          Toast.success(this.$t('message.transferPayment.successCreateOrder'))
          return resolve();
        }).catch(err => {
          return reject(err);
        })
      });
    },
    getBaseName(base) {
      if (base === "CNT") {
        return "CNY";
      } else if (base === "SWTC") {
        return "SWT";
      } else {
        return base;
      }
    },
    orderWithtradePsw(coin) {
      if (!this.isValidAddress(this.form.walletAddress)) {
        Toast.fail(this.$t('message.transferPayment.toAddressVaild'));
        this.clearInput();
        return;
      }
      let jcTool = new JcWalletTool(this.jcWallet);
      let proEnv = process.env.NODE_ENV === "production";
      let hosts = proEnv ? this.exHosts : [window.location.hostname];
      let port = proEnv ? this.exPort : "8080";
      jcTool
        .validatePassword(this.form.tradePsw, "swt")
        .then(secret => {
          var data = {};
          data.issuer = process.env.currencies.UST.issuer;
          data.currency = coin;
          data.address = this.publicKey;
          data.to = this.form.walletAddress;
          data.amount = this.form.inputTargetCount;
          data.memo = this.form.purpose;
          data.secret = secret;
          data.hosts = hosts;
          data.port = port;
          data.https = this.https;
          setTimeout(() => {
            this.confirmTransfer(data);
          }, 1000);
        })
        .catch(error => {
          Toast.fail(error.message);
        })
        .finally(() => {});
    },
    confirmTransfer(data) {
      console.log("");
      console.log(data);
      transferAccount(data)
        .then(() => {
          Toast.success(this.$t('message.transferPayment.finishAgiotage'));
        })
        .catch(error => {
          console.log(error.message);
          Toast.fail(error.message);
        });
    },
    isValidAddress(address, chain = "swt") {
      return Wallet.isValidAddress(address, chain);
    }
  },
  computed: {
    sum() {
      let price = new BigNumber(this.form.price).decimalPlaces(3).toNumber();
      let buyAmount = this.form.inputTargetCount;
      let sellAmount = this.form.amount;
      let sum = 0;
      if (this.type === 0) {
        if (isNumber(parseFloat(price)) && isNumber(parseFloat(buyAmount))) {
          sum = new BigNumber(buyAmount).decimalPlaces(6).multipliedBy(price).decimalPlaces(6).toNumber();
        }
      } else if (this.type === 1) {
        if (isNumber(parseFloat(price)) && isNumber(parseFloat(sellAmount))) {
          sum = new BigNumber(sellAmount).decimalPlaces(6).multipliedBy(price).decimalPlaces(6).toNumber();
        }
      }
      return sum;
    },
    getAmount() {
      if (this.type === 0) {
        return this.form.inputTargetCount;
      } else if (this.type === 1) {
        return this.form.amount;
      }
    },
    publicKey() {
      return this.$store.getters.swtAddress;
    },
    jcWallet() {
      return this.$store.getters.jcWallet;
    }
  }
}


